import {api} from "../api/api";
import * as TYPE from "../constants/cartConstants";

export const addToCart = (productId, qty) => async (dispatch, getState) => {
  try {
    const { data } = await api.get(`/api/products/${productId}`);
    dispatch({
      type: TYPE.CART_ADD_ITEM,
      payload: {
        name: data.name,
        image: data.image,
        price: data.price,
        countInStock: data.countInStock,
        product: data._id,
        qty,
      },
    });
  } catch (error) {}
  localStorage.setItem("cartItems", JSON.stringify(getState().cart.cartItems));
};

export const saveShippingAddress = (data) => async (dispatch, getState) => {
  dispatch({ type: TYPE.CART_SAVE_SHIPPING_ADDRESS, payload: data });
  localStorage.setItem("shippingAddress", JSON.stringify(data));
};
export const savePaymentMethod = (data) => async (dispatch, getState) => {
  dispatch({ type: TYPE.CART_SAVE_PAYMENT_METHOD, payload: data });
};

export const removeFromCart = (productId) => async (dispatch, getState) => {
  dispatch({ type: TYPE.CART_REMOVE_ITEM, payload: productId });
  localStorage.setItem("cartItems", JSON.stringify(getState().cart.cartItems));
};
